<template>
  <div>
    <el-form inline>

      <!--  -->
      <el-form-item label="">
        <el-date-picker
            size="mini"
            style="width: 175px"
            v-model="startTime"
            type="datetime"
            placeholder=""
            default-time="12:00:00"
            value-format="yyyy-MM-dd hh:mm:ss"
            @change="selectChange(1)"
        >
        </el-date-picker>
      </el-form-item>

      <!--  -->
      <el-form-item label="">
        <el-date-picker
            size="mini"
            style="width: 175px"
            v-model="endTime"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd hh:mm:ss"
            default-time="12:00:00"
            @change="selectChange(2)"
        >
        </el-date-picker>
      </el-form-item>

      <el-button
          @click="workPath"
          type="primary"
          size="mini">
      </el-button>

    </el-form>
  </div>
</template>

<script>

export default {
  data() {
    return {
      //  -----------  --------------
      startTime: undefined,
      endTime: undefined,
    }
  },
  methods: {
    // --------------  --------------

    /**
     * 
     * */
    selectChange: function (index) {
      if (this.startTime === undefined || this.endTime === undefined) {
        return 0
      }
      let s_obj = new Date(this.startTime);
      let e_obj = new Date(this.endTime);
      if (s_obj.getTime() >= e_obj.getTime()) {
        this.$message("!");
        switch (index) {
          case 1:
            this.startTime = undefined;
            break;
          case 2:
            this.endTime = undefined;
            break;
          default:
            break;
        }
        return 0
      }
    },

    /**
     * 
     * */
    workPath: function () {
      this.$emit("workPath", [this.startTime, this.endTime]);
    },
  }
}
</script>

<style>

.el-form {
  text-align: left;
}

.el-form-item {
  margin-left: 1.5vw;
}
</style>